import { getTestSettings } from '@/lib/questions';
import type { Difficulty, TestType } from '@/types';
import TestPageClient from '@/components/TestPageClient';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Terminal } from 'lucide-react';
import Link from 'next/link';
import { Button } from '@/components/ui/button';

export default function TestPage({ params }: { params: { testId: string; difficulty: string } }) {
  const { testId, difficulty } = params;

  const settings = getTestSettings(testId as TestType, difficulty as Difficulty);

  if (!settings) {
    return (
        <div className="max-w-lg mx-auto">
            <Alert variant="destructive">
                <Terminal className="h-4 w-4" />
                <AlertTitle>Invalid Test</AlertTitle>
                <AlertDescription>
                    The test you are trying to access does not exist. Please go back and select a valid test.
                </AlertDescription>
                 <div className="mt-4">
                    <Button asChild>
                        <Link href="/tests">Go to Tests</Link>
                    </Button>
                </div>
            </Alert>
        </div>
    );
  }

  return <TestPageClient testId={testId as TestType} difficulty={difficulty as Difficulty} settings={settings} />;
}
